import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Location } from '@angular/common';
import { CoreConfig } from './core-config';

@Injectable({
  providedIn: 'root'
})
export class CoreConfigurationService {
  private config: CoreConfig = null;

  constructor(private http: HttpClient, private location: Location) {
  }

  public load(url: string): Promise<CoreConfig> {

    const configFilePath = this.location.prepareExternalUrl(url);

    return this.http.get(configFilePath)
      .toPromise()
      .then((config: CoreConfig) => {
        this.config = config;
        return config;
      })
      .catch(error => {
        console.error(error);
        return null;
      });
  }

  public getConfig(): any {
    return this.config;
  }
}
