import { AfterContentInit, Component, ContentChildren, forwardRef, OnDestroy, OnInit, QueryList } from '@angular/core';
import { CommandbarInlinefilterComponent } from '../inlinefilter/commandbar-inlinefilter.component';
import { merge, of, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ObservableMedia } from '@angular/flex-layout';

@Component({
  selector: 'bci-commandbar-inlinefilters',
  template: `<!--
  ~ Copyright (C) 2018 Robert Bosch Manufacturing Solutions GmbH, Germany. All rights reserved.
  -->

<div fxLayout="row">
  <ng-container *ngFor="let item of visibleItems">
    <ng-container *ngTemplateOutlet="item?.template"></ng-container>
  </ng-container>
</div>
`,
  styles: [`:host{vertical-align:middle;line-height:48px}`]
})
export class CommandbarInlinefiltersComponent implements OnInit, OnDestroy, AfterContentInit {

  @ContentChildren(forwardRef(() => CommandbarInlinefilterComponent)) items: QueryList<CommandbarInlinefilterComponent>;
  public visibleItems: CommandbarInlinefilterComponent[] = [];
  public hiddenItems: CommandbarInlinefilterComponent[] = [];

  private componentDestroyed$ = new Subject();

  constructor(private observableMedia: ObservableMedia) {
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.componentDestroyed$.next();
    this.componentDestroyed$.complete();
  }

  ngAfterContentInit() {
    // listen to changes of viewport size
    merge(this.observableMedia.asObservable(), this.items.changes, of(true))
      .pipe(
        takeUntil(this.componentDestroyed$)
      )
      .subscribe(() => {
        this.updateView();
      });
  }

  private updateView() {
    const visible = [];
    const hidden = [];
    this.items.forEach(item => this.trueOrMatchesMediaState(item.hide) ? hidden.push(item) : visible.push(item));

    this.visibleItems.length = 0;
    this.hiddenItems.length = 0;
    if (hidden.length === 1) {
      // no more-actions-menu for a single hidden action
      this.visibleItems.push(...this.items.toArray());
    } else {
      this.visibleItems.push(...visible);
      this.hiddenItems.push(...hidden);
    }
  }

  private trueOrMatchesMediaState(value: string | boolean) {
    if (value === undefined) {
      return false;
    } else if (value === null) {
      return true;
    } else if (typeof value === 'boolean') {
      return value;
    } else {
      return value.length === 0 || this.observableMedia.isActive(value);
    }
  }

}
