import { Directive, Input, OnInit } from '@angular/core';
import { CommandbarButtonComponent } from './button/commandbar-button.component';

@Directive({
  selector: `[bciCommandbarActionAdd],[bciCommandbarActionEdit],[bciCommandbarActionDelete]`,
})
export class CommandbarButtonsDirective implements OnInit {

  private icon: string;

  constructor(private host: CommandbarButtonComponent) {
  }

  @Input('bciCommandbarActionAdd')
  set buttonAdd(_) {
    this.setIcon('Bosch-Ic-add-outline');
  }

  @Input('bciCommandbarActionEdit')
  set buttonEdit(_) {
    this.setIcon('Bosch-Ic-edit');
  }

  @Input('bciCommandbarActionDelete')
  set buttonDelete(_) {
    this.setIcon('Bosch-Ic-delete');
  }

  ngOnInit(): void {
    this.host.icon = this.icon;
  }

  private setIcon(icon: string) {
    this.icon = icon;
  }
}
