import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Location } from '@angular/common';
import { ImprintModel } from './imprint.model';

@Injectable({
  providedIn: 'root'
})
export class BciImprintDetailsService {

  constructor(private httpClient: HttpClient, private location: Location) {
  }

  /**
   * Get imprint details for imprint view.
   */
  getImprintDetails(): Observable<ImprintModel> {
    return this.httpClient.get<ImprintModel>(this.location.prepareExternalUrl('/assets/imprint-details.json'));
  }
}
